import CSVParse from "papaparse";
import csvTable from "../constants/csvTables";

export const getTableFromQuery = (query) => {
  let table = null;
  if (query.includes("employees")) table = csvTable.employees;
  else if (query.includes("customers")) table = csvTable.customers;
  else if (query.includes("categories")) table = csvTable.categories;
  else if (query.includes("employee_territories")) table = csvTable.employee_territories;
  else if (query.includes("order_details")) table = csvTable.order_details;
  else if (query.includes("orders")) table = csvTable.orders;
  else if (query.includes("products")) table = csvTable.products;
  else if (query.includes("regions")) table = csvTable.regions;
  else if (query.includes("shippers")) table = csvTable.shippers;
  else if (query.includes("suppliers")) table = csvTable.suppliers;
  else if (query.includes("territories")) table = csvTable.territories;
  else table = csvTable.categories;
  return table;
};

export const fetchCSVData = async (csv) => {
  const response = await fetch(csv);
  const text = await response.text();
  const result = CSVParse.parse(text, { header: true }).data;
  return result;
};
